<template>
  <div class="mask-body" v-ban-save>
    <Header name="购物车"></Header>
    <div class="head">
      <p class="text2" @click="changeState">{{ editFont }}</p>
    </div>
    <div class="body">
      <div v-if="cartList && cartList.length > 0">
        <div class="shopCartt" v-for="item in cartList" :key="item.id">
          <div class="top">
            <van-checkbox v-model="item.flag" checked-color="#ee0a24" @click="selectClickData(item)" :disabled="item.quantity > item.realStock || item.offlineBuy === 1"></van-checkbox>
            <div class="shopImg" @click="goShop(item)">
              <van-image :src="item.productPic ? item.productPic : ''" style="width: 100%; height: 100%;"/>
              <div v-if="item.memberDiscount" class="discount">享{{accMul(item.memberDiscount,10)}}折</div>
            </div>
            <div class="shopMsg">
              <div class="shopName">
                {{item.productName}}
              </div>
              <div class="shopSpecs" v-if="item.productAttr">
                <div v-for="(ite,i) in JSON.parse(item.productAttr || `[]`)" :key="i">{{ite.value}}</div>
              </div>
              <div v-else style="min-height: 20px;"></div>
              <div class="shopIntegral">赠送{{item.memberDiscount ? Math.floor(Number(accMul(accMul(item.price , item.memberDiscount), item.quantity))) : Math.floor(Number(accMul(item.price , item.quantity)))}}积分</div>
            </div>
          </div>
          <div class="bottom">
            <div></div>
            <!-- <div class="bomRight"><div class="amount">￥{{item.price * item.quantity}}</div> -->
            <div class="bomRight"><div class="amount">￥{{accMul(item.price , item.quantity)}}</div>
            <div class="num">
              <van-stepper v-model="item.quantity" async-change button-size="20px" @change="updQuantity(item)" input-width="36px" integer :max="item.maxQuantity" :min="item.minimumNum"/>
            </div></div>
            
          </div>
        </div>
      </div>
      <div v-else class="showNo">
        暂无数据
      </div>
    </div>
    <div class="footer">
      <div class="allchoice">
        <van-checkbox @click="selectClickAll" v-model="checkedAll" checked-color="#ee0a24">全选</van-checkbox>
      </div>
      <div v-if="edit">
        <div class="price">
          <div class="total">总计：<span class="allMon">￥{{(numTotal).toFixed(2)}}</span></div>
          <div class="preferential">优惠：<span>￥{{numReduce}}</span></div>
        </div>
        <div class="closeBtn" @click="nowSettle">立即结算</div>
      </div>
      <div v-else>
        <div class="deleteBtn" @click="deleteAll">删除</div>
      </div>
    </div>
    <div style="width:100%;height:94px"></div>
    <div class="footers">
      <div class="firstPage" @click="toPages('0')">
        <img :src="require('@/assets/image/xzsy_gray.png')" alt="" />
        <p>首页</p>
      </div>
      <div class="sort" @click="toPages('1')">
        <img :src="require('@/assets/image/wxzfl_gray.png')" alt="" />
        <p>分类</p>
      </div>
      <div class="shopCart" @click="toPages('2')">
        <img :src="require('@/assets/image/wxzgwc.png')" alt="" />
        <p class="ptitle">购物车</p>
      </div>
      <div class="myMessage" @click="toPages('3')">
        <img :src="require('@/assets/image/wxzmy_gray.png')" alt="" />
        <p>我的</p>
      </div>
    </div>
    <Service></Service>
  </div>
</template>

<script>
import Service from '@/components/Layout/service/index'
import Header from '@/components/Layout/header/index'
import * as api from '@/data/api/market/index'
export default {
  name: 'home',
  components :{
    Service,
    Header
  },
  data () {
    return {
      userType: 0,
      buyPower: '',
      buyArr: [],
      checkedAll: false,
      edit: true,
      editFont: "编辑",
      cartList:[],
      numTotal:0.00,
      numReduce:0.00,
      checkqx: 0,
      wxUrl: process.env.wxUrl
    }
  },
  mounted () {
    this.getCartList()
  },
  methods: {
    getCartList(){
      let me = this
      if (localStorage.getItem('token')) {
        api.getCartList().then(res => {
          if (res.code === 200) {
            let data = res.data
            api.memberIntegration().then(res=>{
              me.buyPower = res.data.memberLevel
              me.userType = res.data.userType
              me.numTotal = 0.00
              me.numReduce = 0.00
              data.map((item, index) => {
                item.flag = false
                item.maxQuantity = item.realStock
                item.buyArr = false
                if(item.memberProductList.length === 0){
                  item.buyArr = true
                }else{
                  item.memberProductList.forEach(i=>{
                    if(i.memberType === me.userType){
                      if(me.buyPower === i.memberLevelName){
                        item.buyArr = true
                      }
                    }
                  })
                }
                return item
              })
              me.cartList = data
            })
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
          }).catch((res) => {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }).finally(() => {
          })
      } else {
        me.numTotal = 0.00
        me.cartList = []
      }
    },
    goShop(item){
      this.$router.push({
        path:'/market/product',
        query: {id: item.productId}
      })
    },
    toPages (value) {
      if (value === '0') {
        this.$router.push({
          path:'/market/home'
        })
      } else if (value === '1') {
        this.$router.push({
          path:'/market/classify'
        })
      } else if (value === '2') {
        if (localStorage.getItem('token')) {
          this.$router.push({
            path:'/market/cart'
          })
        } else {
          const { userAgent } = navigator
          this.isWeixin = !!/MicroMessenger/i.test(userAgent)
          // !localStorage.getItem('token') && 
          if (this.isWeixin) {
            let st = {
              name: 'home',
              url: '/market/cart'
            }
            window.location.href="https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx372e6d05b22ce032&redirect_uri=" + this.wxUrl + "&response_type=code&scope=snsapi_base&state=" + JSON.stringify(st) + "#wechat_redirect"
          } else {
            localStorage.setItem('urlPath','/market/cart')
            this.$router.push({
              path:'/user/login'
            })
          }
        }
      } else if (value === '3') {
        if (localStorage.getItem('token')) {
          this.$router.push({
            path:'/user/home'
          })
        } else {
          
          const { userAgent } = navigator
          this.isWeixin = !!/MicroMessenger/i.test(userAgent)
          // !localStorage.getItem('token') && 
          if (this.isWeixin) {
            let st = {
              name: 'home',
              url: '/user/home'
            }
            window.location.href="https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx372e6d05b22ce032&redirect_uri=" + this.wxUrl + "&response_type=code&scope=snsapi_base&state=" + JSON.stringify(st) + "#wechat_redirect"
          } else {
            localStorage.setItem('urlPath','/user/home')
            this.$router.push({
              path:'/user/login'
            })
          }
        }
      }
    },
    changeState() {
      this.edit = !this.edit;
      if (!this.edit) {
        this.editFont = "取消";
      } else {
        this.editFont = "编辑";
      }
    },
    nowSettle(){
      let me = this
      me.$nextTick(() => {
        let ids = []
        me.buyArr = []
        me.cartList.map((item, index) => {
          if (item.flag) {
            ids.push(item.id)
            if(!item.buyArr){
              me.buyArr.push({id: item.id,name: item.productName})
            }
          }
        })
        if (ids.length > 0) {
          if(me.buyArr.length === 0){
            let isCoil = false
            let name = ''
            me.cartList.forEach(itm => {
              if(ids.includes(itm.id)) {
                if(itm.publishStatus === 0) {
                  isCoil = true
                  name = itm.productName
                }
              }
            })
            if(isCoil) {
              me.$notify({
                message: `${name}该商品已下架，无法购买`,
                color: '#ad0000',
                background: '#ffe1e1',
              })
            } else {
              localStorage.setItem('cartIds', ids)
              me.$router.push({
                path:'/user/orders'
              })
            }
            
          }
          me.buyArr.forEach(item=>{
            if(ids.includes(item.id)){
              me.$notify({
                message: `您没有权限购买${item.name}`,
                color: '#ad0000',
                background: '#ffe1e1',
              })
            }
          })
        } else {
          me.$notify({
            message: '请选择需要购买的商品',
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }, 500)
    },
    deleteAll () {
      let me = this
      me.cartList.map((item, index) => {
        if (me.checkedAll) {
          me.deleteDetail(item)
        } else {
          if (item.flag) {
            me.deleteDetail(item)
          }
        }
      })
    },
    deleteDetail (item) {
      let me = this
      me.checkedAll = false
      me.selectClickData(item)
      api.cartDelete({ids: item.id}).then(res => {
        if (res.code === 200) {
          me.$nextTick(() => {
            me.getCartList()
          }, 500);
        } else {
          me.$nextTick(() => {
            me.getCartList()
          }, 500);
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }).finally(() => {
      })
    },
    selectClickAll () {
      let me = this
      me.numTotal= 0.00
      me.numReduce = 0.00
      if (me.checkedAll) {
        me.cartList.map((item, index) => {
          if(item.quantity > item.realStock || item.offlineBuy === 1){
            item.flag = false
          }else{
            me.numTotal = me.accAdd(me.numTotal , me.accMul(item.price , item.quantity))
            me.numReduce = item.reduceAmount !== 0 ? me.accAdd(me.numReduce, me.accMul(item.reduceAmount , item.quantity)) : me.numReduce
            me.checkqx = me.cartList.length;
            item.flag = true
          }
          return item
        })
      } else {
        me.numTotal = 0.00
        me.numReduce = 0.00
        me.checkqx = 0
        me.cartList.map((item, index) => {
          item.flag = false
          return item
        })
      }
      this.resetNumTotal()
    },
    selectClickData (item) {
      let me = this
      me.numReduce = 0.00
      if(item.quantity > item.realStock){
        return
      }
      if (item.flag) {
        me.checkqx += 1;
        if(me.checkqx === me.cartList.length){
          me.checkedAll = true;
        }
        me.numTotal = me.accAdd(me.numTotal , me.accMul(item.price , item.quantity))
        me.numReduce = item.reduceAmount !== 0 ? me.accAdd(me.numReduce, me.accMul(item.reduceAmount , item.quantity)) : me.numReduce
      } else {
        me.checkedAll = false;
        me.checkqx -= 1;
        if(me.checkqx < 0){
          me.checkqx = 0;
        }
        me.numTotal = me.accSub(me.numTotal , me.accMul(item.price , item.quantity))
        me.numReduce = item.reduceAmount !== 0 ? me.accAdd(me.numReduce, me.accMul(item.reduceAmount , item.quantity)) : me.numReduce
      }
      this.resetNumTotal()
    },
    updQuantity(item){
      let me = this
      api.cartUpdateQuantity({id: item.id, quantity: item.quantity}).then(res => {
        if (res.code === 200) {
        } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }).finally(() => {
        this.resetNumTotal()
      })
    },
    resetNumTotal () {
      let me = this
      me.numTotal = 0.00
      me.numReduce = 0.00
      if (me.checkedAll) {
        me.cartList.map((item, index) => {
          me.numTotal = me.accAdd(me.numTotal , item.memberDiscount ? me.accMul(me.accMul(item.price , item.quantity),item.memberDiscount) : me.accMul(item.price , item.quantity))
          me.numReduce = item.reduceAmount !== 0 ? me.accAdd(me.numReduce, me.accMul(item.reduceAmount , item.quantity)) : me.numReduce
        })
      } else {
        me.cartList.map((item, index) => {
          if (item.flag) {
            me.numTotal = me.accAdd(me.numTotal , item.memberDiscount ? me.accMul(me.accMul(item.price , item.quantity),item.memberDiscount) : me.accMul(item.price , item.quantity))
            me.numReduce = item.reduceAmount !== 0 ? me.accAdd(me.numReduce, me.accMul(item.reduceAmount , item.quantity)) : me.numReduce
          }
        })
      }
    },
    accMul (arg1, arg2) {
      if (arg1 && arg2) {
        let m = 0,
          s1 = arg1.toString(),
          s2 = arg2.toString();
        try {
          m += s1.split('.')[1].length;
        } catch (e) {}
        try {
          m += s2.split('.')[1].length;
        } catch (e) {}
        return (
          (Number(s1.replace('.', '')) * Number(s2.replace('.', ''))) /
          Math.pow(10, m)
        )
      }
    },
    accAdd (arg1, arg2) {
      if ((arg1 || arg1 === 0) && (arg2 || arg1 === 0)) {
        let r1, r2, m;
        try {
          r1 = arg1.toString().split('.')[1].length;
        } catch (e) {
          r1 = 0;
        }
        try {
          r2 = arg2.toString().split('.')[1].length;
        } catch (e) {
          r2 = 0;
        }
        m = Math.pow(10, Math.max(r1, r2));
        return (arg1 * m + arg2 * m) / m;
      }
    },
    accSub (arg1, arg2) {
      if ((arg1 || arg1 === 0) && (arg2 || arg1 === 0)) {
        let r1, r2, m, n;
        try {
          r1 = arg1.toString().split('.')[1].length;
        } catch (e) {
          r1 = 0;
        }
        try {
          r2 = arg2.toString().split('.')[1].length;
        } catch (e) {
          r2 = 0;
        }
        m = Math.pow(10, Math.max(r1, r2));
        n = r1 >= r2 ? r1 : r2;
        return ((arg1 * m - arg2 * m) / m).toFixed(2);
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
@import '~@/styles/variable.styl';
>>>.van-image {
  img {
    pointer-events: none;
  }
}
.mask-body {
  min-height: 700px;
  background: #f6f6f6;
}
>>>.van-stepper{
  width 80px!important
  background #fff!important
  .van-stepper__minus--disabled{
    color: #c8c9cc;
  }
  button{
    color #ff7300
    background #fff
  }
}
.head {
  width: 100%;
  height: 40px;
  background-color: #fff;
  position: relative;
  top: 0px;
  z-index: 999;

  .text1 {
    width: 48px;
    height: 22px;
    font-size: 16px;
    font-family: PingFangSC-Medium, PingFang SC;
    font-weight: 500;
    color: #303030;
    line-height: 22px;
    position: absolute;
    left: 10px;
    top: 9px;
  }

  .text2 {
    width: 28px;
    height: 14px;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #FF4C4C;
    line-height: 14px;
    position: absolute;
    right: 15px;
    top: 14px;
  }
}

.body {
  .shopCartt {
    width: 94%; // 355px
    // height: 167px;
    background: #FFFFFF;
    box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.02);
    border-radius: 12px;
    margin: 10px 3%;
    padding:20px 0px

    .top {
      // position: relative;
      width: 95.8%; // 340px
      // height: 88px;
      display: flex;
      margin-left: 4%;

      .checked {
        // position: relative;
        top: 32px;
        width: 20px;
        height: 20px;
        background: rgba(216, 216, 216, 0);
        border: 1px solid #B9B9B9;
        border-radius: 50%;
      }

      .shopImg {
        // position relative
        width: 80px;
        height: 80px;
        margin-left: 1.5%;
        position:relative
        .discount {
          position: absolute;
          right: 0px;
          top: 0px;
          width: 48px;
          height: 23px;
          background: linear-gradient(155deg, #F48D1C 0%, #E6460C 100%);
          border-radius: 0px 0px 0px 18px;
          font-size: 10px;
          text-align: center;
          line-height: 23px;
          color: #fff;
        }
      }

      .shopMsg {
        width: 65%;
        // height: 88px;
        margin-left: 2.4%;

        .shopName {
          width: 95%;
          height: 38px
          line-height: 20px;
          font-weight: bold;
          word-break: break-all;
          text-overflow: ellipsis;
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
        }

        .shopSpecs {
          display: flex;
          flex-wrap: wrap;
          font-size: 13px;
          margin-bottom: 5px;
          font-weight: 400;
          color: #888888;

            div{
              margin-right: 10px;
              margin-bottom: 5px;
            }
        }

        .shopIntegral {
          width: fit-content;
          padding: 0 5px;
          height: 20px;
          background: #FEE7D1;
          border-radius: 3px;
          text-align: center;
          line-height: 20px;
          color: #F48A1B;
          font-size: 12px;
        }
      }
    }

    .bottom {
      width: 92%;
      margin: 10px 0px 0px 4%;
      height: 20px;
      display: flex;
      justify-content: space-between;
      .bomRight{
        width: 65%
        display: flex;
        justify-content: space-between;
      }
      .amount {
        height: 14px;
        font-size: 18px;
        font-weight: bold;
        color: #FF2F2F;
        line-height: 14px;
      }

      .num {
        height: 20px;
        display: flex;
        justify-content: space-between;
        .disable{
          cursor not-allowed
          color #b9b9b9
        }
        div {
          width: 36px;
          height: 20px;
          text-align: center;
          line-height: 20px;
          background: #F6F6F6;
          border-radius: 2px;
          font-size: 12px;
        }

        span {
          display: inline-block;
          width: 20px;
          height: 20px;
          text-align: center;
          font-size: 20px;
          color: #FF7300;
        }
      }
    }
  }
  .showNo {
    margin: 10px 0px;
    padding: 50px;
    text-align: center;
    font-size: 15px;
    background-color: #f6f6f6;
    color: #BBBBBB;
  }
}

.footer {
  position: relative;
  width: 100%;
  height: 44px;
  background-color: #fff;
  position: fixed;
  bottom: 49px;

  .allchoice {
    position: absolute;
    left: 4%;
    top: 11px;
    width: 58px;
    height: 20px;
    display: flex;
    justify-content: space-between;

    .choiceIcon {
      width: 20px;
      height: 20px;
      border-radius: 50%;
      border: 1px solid #f44;
      box-sizing: border-box;

      img {
        width: 100%;
        height: 100%;
      }
    }

    p {
      width: 30px;
      height: 11px;
      font-size: 15px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: #444444;
      line-height: 11px;
    }
  }

  .price {
    position: absolute;
    left: 25.6%;
    top: 5px;

    .preferential {
      margin-top: 5px;
    }

    span {
      color: #f44;
      font-size: 14px;
    }

    .allMon {
      font-size: 16px;
      font-weight: bold;
    }
  }

  .closeBtn {
    position: absolute;
    right: 0px;
    width: 29.3%;
    height: 44px;
    font-size: 15px;
    background: #FF4C4C;
    text-align: center;
    line-height: 44px;
    color: #fff;
  }

  .deleteBtn {
    position: absolute;
    top: 9px;
    right: 15px;
    width: 14.6%;
    height: 26px;
    background: #FFFFFF;
    border-radius: 14px;
    border: 1px solid #979797;
    text-align: center;
    line-height: 26px;
  }
}
.footers {
  position fixed
  bottom 0px
  width: 100%;
  height: 49px;
  background: #fff;
  box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.05);
  display flex;
  z-index: 999;
  justify-content space-around
  .firstPage,.sort,.shopCart,.myMessage {
    font-size 10px
    width: 44px;
    height: 37px; 
    margin 6px 0px
    text-align center
  }
  img{
    width 20px;
    height 20px
  }
  .ptitle {
    color: #FF4C4C;
  }
  p{
    color #444
    margin-top 4px
  }
}
</style>



